/* eslint-disable react/prop-types */
import React from 'react';
import { AiOutlineFileText } from 'react-icons/ai';
import { MdOutlineSecurity } from 'react-icons/md';
import { FaUsers } from 'react-icons/fa';
import styled from 'styled-components';
import Toggle from './Toggle';
import { Sidebar, Menu, MenuItem, SidebarHeader, SidebarContent, SidebarFooter } from './Structure';
import SojiLogo from '@components/assets/Logo/soji_2.png';
import SojiBaseInit from '@modules/SojiBase';
import { useNavigate } from 'react-router-dom';
import { GoogleLogout } from 'react-google-login';
import { colors } from '@components/typo';

const LogoContainer = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 10px;
`;

const Logo = styled.img`
    width: 50px;
    height: 26px;
`;

const LogoutContainer = styled.div`
    width: 100%;
    height: 50px;
    background-color: ${colors.defaultColor};
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    cursor: pointer;
`;

const SidebarLayout = ({ image, collapsed, rtl, toggled, handleToggleSidebar, handleCollapsedChange }) => {
    const navigate = useNavigate();

    const logOut = async () => {
        const SOJIBase = await SojiBaseInit;
        await SOJIBase.deleteCookie();
        navigate('/', { replace: true });
        console.log('logout')
    };

    return (
        <Sidebar
            image={false}
            rtl={rtl}
            collapsed={collapsed}
            toggled={toggled}
            breakPoint="md"
            onToggle={handleToggleSidebar}
        >
            <SidebarHeader>
                {!collapsed && (
                    <LogoContainer>
                        <Logo src={SojiLogo} />
                    </LogoContainer>
                )}
                <Toggle handleToggleSidebar={handleCollapsedChange} collapsed={collapsed} />
            </SidebarHeader>

            <SidebarContent>
                <Menu iconShape="circle">
                    <MenuItem to="/company/dashboard/analysis" icon={<AiOutlineFileText color="#82878B" size={20} />}>
                        Analysis
                    </MenuItem>
                    <MenuItem to="/company/dashboard/documents" icon={<MdOutlineSecurity color="#82878B" size={20} />}>
                        Contents
                    </MenuItem>
                    <MenuItem to="/company/dashboard/reviews" icon={<MdOutlineSecurity color="#82878B" size={20} />}>
                        Reviews
                    </MenuItem>
                    <MenuItem to="/company/dashboard/visitors" icon={<MdOutlineSecurity color="#82878B" size={20} />}>
                        Visitors
                    </MenuItem>
                </Menu>
            </SidebarContent>
            <SidebarFooter>
                <LogoutContainer onClick={logOut}>
                    {/* <GoogleLogout
                        clientId="178683516706-dskp14ak0s9vivcnauhs78ur84gl008c.apps.googleusercontent.com"
                        buttonText="Logout"
                        onLogoutSuccess={logOut}
                        render={(renderProps) => <div onClick={renderProps.onClick} disabled={renderProps.disabled}>Logout</div>}
                    ></GoogleLogout> */}
                    Logout
                </LogoutContainer>
            </SidebarFooter>
        </Sidebar>
    );
};

export default SidebarLayout;
