import React from 'react';
import styled from 'styled-components';
import Header from '../../layouts/Header';

import ReviewFeed from './componets/FeedCard';
import ReviewStats from './componets/ReviewStats';
import './index.css';

const TitleBox = styled.div`
    font-weight: 600;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 15px;
    margin-top: 15px;
    width: 100%;
    text-align: left;
`;

export default function Reviews() {
    return (
        <div>
            <TitleBox>Reviews</TitleBox>
            <div className="row" style={{ width: '100%' }}>
                <div className="col-md-7">
                    <ReviewFeed />
                    <ReviewFeed />
                    <ReviewFeed />
                    <ReviewFeed />
                    <ReviewFeed />
                    <ReviewFeed />
                    <ReviewFeed />
                    <ReviewFeed />
                    <ReviewFeed />
                    <ReviewFeed />
                    <ReviewFeed />
                    <ReviewFeed />
                    <ReviewFeed />
                    <ReviewFeed />
                </div>
                <div className="col-md-5 order-first order-md-last">
                    <ReviewStats />
                </div>
            </div>
        </div>
    );
}
